<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-input v-model="event_name" prefix-icon="el-icon-search" placeholder="事件名称" clearable>
					</el-input>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-select v-model="cash_main_type" clearable placeholder="现金流主体类型">
						<el-option v-for="item in cash_main_type_list" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-input v-model="cash_main_name" placeholder="现金流主体名称" clearable>
					</el-input>
				</div>
				<div class="search_box">
					<el-select v-model="cash_type" clearable placeholder="现金流类型">
						<el-option v-for="item in cash_type_list" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="btn_box">
					<button @click="getInitData(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="table_title flex_between">
			<div class="">
				现金流数据总数<span>(&nbsp;{{ total }}&nbsp;)</span>
			</div>
			<div class="flex_end">
				<button class="blue_btn" @click="downloadExcel">
					<span class="iconfont icon-daoru"></span>
					导出excel表格
				</button>
			</div>
		</div>
		<div class="table">
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column sortable prop="id" label="序号" width="80">
					<template slot-scope="scope">
						{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="date" label="日期" width="200">
				</el-table-column>
				<el-table-column sortable prop="fund_type_name" label="主体类型" width="180">
				</el-table-column>
				<el-table-column sortable prop="fund_name" label="现金流主体名称" width="180">
				</el-table-column>
				<el-table-column sortable prop="cache_flow_type_name" label="现金流类型" width="180">
				</el-table-column>
				<el-table-column sortable prop="event" label="事件" width="150">
				</el-table-column>
				<el-table-column sortable prop="fee" label="金额" width="180">
				</el-table-column>
				<el-table-column sortable prop="note" label="备注" width="150">
				</el-table-column>
				<template slot="empty">
					<div class="table-nodata flex_start">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span> 
					</div>
				</template>
			</el-table>
			<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getListReportCashApi,downloadExcelReportCashApi } from '../../config/api'
	
	export default {
		name: 'cashFlowDataTotal',
		data() {
			return {
				// 搜索字段开始
				event_name: '',
				cash_main_type: '',
				cash_main_name: '',
				cash_type: '',
				// 搜索字段结束
				total: 0,
				currentPage: 1,
				pageSize: 10,
				tableData: [ ],
				
				cash_main_type_list: [
					{
						value: 1,
						label: '母基金'
					},
					{
						value: 2,
						label: '自管基金'
					},
					{
						value: 3,
						label: '投资人'
					},
					{
						value: 4,
						label: '子基金'
					},
					{
						value: 5,
						label: '直投项目'
					},
					{
						value: 6,
						label: '管理公司'
					},
				],
				cash_type_list: [
					{
						value: 1,
						label: '分配'
					},
					{
						value: 2,
						label: '收入'
					},
					{
						value: 3,
						label: '投资'
					},
					{
						value: 4,
						label: '支出'
					},
					{
						value: 5,
						label: '回款'
					},
					{
						value: 6,
						label: '缴款'
					},
					{
						value: 7,
						label: '支出-管理费'
					},
					
					// {
					// 	value: 10,
					// 	label: '回款收益'
					// },
					// {
					// 	value: 11,
					// 	label: '回款本金'
					// },
					{
						value: 12,
						label: '收入-管理费'
					},
					{
						value: 13,
						label: '收入-认购服务费'
					},
				],
			}
		},
		created() {
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				getListReportCashApi({
					event_search_content: this.event_name,
					fund_search_content: this.cash_main_name,
					cache_flow_type: this.cash_type,
					fund_type: this.cash_main_type,
					page: this.currentPage,
					size: this.pageSize
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				downloadExcelReportCashApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 !important;
	}
	.search_group{
		padding: 30px 0;
    		border-bottom: 1px solid #F1F1F1;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.table_title{
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.table_title span{
		color: #6292F4;
	}
</style>


